import * as React from "react";
import Dialog from "@mui/material/Dialog";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Slide from "@mui/material/Slide";
import { IoClose } from "react-icons/io5";
import { TiArrowBack } from "react-icons/ti";
import styled from "styled-components";

import { useMarksEntry } from "../../../Context/MarksEntry";

const MarksActionContainer = React.lazy(() => import("./ActionMarksContainer"));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AddMarks() {
  const marks = useMarksEntry();

  React.useEffect(() => {}, [marks]);

  const handleClose = () => {
    marks.handleOpenBox(false);
  };

  return (
    <div>
      {marks.openBox && (
        <Dialog
          fullScreen
          open={marks.openBox}
          onClose={handleClose}
          TransitionComponent={Transition}
        >
          {/**
           * Top Tools Bar
           */}
          <ToolsBar />
          <MarkContainer>
            <MarksActionContainer />
          </MarkContainer>
        </Dialog>
      )}
    </div>
  );
}

function ToolsBar() {
  const marks = useMarksEntry();
  return (
    <AppBar
      sx={{
        position: "relative",
      }}
    >
      <Toolbar>
        <div style={{ width: "45px" }}>
          {marks.isBack ? (
            <IconButton
              edge="end"
              color="inherit"
              onClick={() => marks.handleBackToOption(false)}
              aria-label="close"
            >
              <TiArrowBack />
            </IconButton>
          ) : (
            <IconButton
              edge="end"
              color="inherit"
              onClick={() => {
                marks.handleOpenBox(false);
                marks.handleBackToOption(false);
              }}
              aria-label="close"
            >
              <TiArrowBack />
            </IconButton>
          )}
        </div>

        <Typography
          sx={{
            ml: 2,
            flex: 1,
          }}
          variant="h6"
          component="div"
        >
          {marks.title}
        </Typography>
        {/* <Button autoFocus color="inherit" onClick={handleClose}>
       save
      </Button> */}
        <p
          className="courseTitle"
          style={{
            marginBottom: "0",
            marginRight: "2rem",
          }}
        >
          {marks.classInfo.courseName + " - Term: " + marks.classInfo.term}
        </p>
        <Icon>
          <IconButton
            color="inherit"
            onClick={() => marks.handleOpenBox(false)}
            aria-label="close"
          >
            <IoClose />
          </IconButton>
        </Icon>
      </Toolbar>
    </AppBar>
  );
}

const MarkContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin-top: 0.5rem;
`;

const Icon = styled.div`
  width: 45px;
  height: 45px;
  border: 2px solid #fff;
  border-radius: 50px;
  display: flex;
  align-items: center;
`;
